import React, { useEffect, useState } from "react";
import SideProject from "./SideProject";
import { Stacks } from "../utils/Stacks";


export default function SideProjects()
{
    const [projects, setProjects] = useState([]);

    useEffect(() =>
    {
        setProjects(
        [
            {
                name: "Bienes Races",
                description: "Aplicacin web para compraventa de inmuebles, con anuncios destacados, blog, vistas detalladas de propiedades y formulario de contacto.",
                imagePath: "bienes-raices.jpg",
                repositoryName: "bienes-raices",
                demoPage: "",
                stacks: [Stacks.springboot, Stacks.mysql, Stacks.bootstrap, Stacks.javascript, Stacks.jquery]
            },
            {
                name: "GDLWEBCAMP",
                description: "Sitio web para una conferencia de diseo web con resumen de eventos, fechas, invitados y funcionalidades interactivas.",
                imagePath: "gdlwebcamp.jpg",
                repositoryName: "gdlwebcamp",
                demoPage: "https://enzogsierra.github.io/gdlwebcamp/",
                stacks: [Stacks.springboot, Stacks.mysql, Stacks.bootstrap, Stacks.javascript, Stacks.jquery]
            },
            {
                name: "Barbershop",
                description: "Aplicacin web para barbera, permitiendo a los usuarios reservar fechas y elegir los servicios de barbera deseados.",
                imagePath: "barbershop.jpg",
                repositoryName: "barbershop",
                demoPage: "",
                stacks: [Stacks.springboot, Stacks.mysql, Stacks.bootstrap, Stacks.javascript, Stacks.jquery]
            },
            {
                name: "Festival de Msica",
                description: "Pgina publicitando un festival de msica. Muestra la fecha del evento, los escenarios, artistas, galera de fotos y e informacin de boletos.",
                imagePath: "festival-de-musica.jpg",
                repositoryName: "festival-de-musica",
                demoPage: "https://enzogsierra.github.io/festival-de-musica/",
                stacks: [Stacks.springboot, Stacks.mysql, Stacks.bootstrap, Stacks.javascript, Stacks.jquery]
            },
            {
                name: "Blog del Caf",
                description: "Pgina web promocionando un blog de caf, mostrando informacin de recetas, tcnicas, cursos, talleres, etc.",
                imagePath: "blogdecafe.jpg",
                repositoryName: "blogdecafe",
                demoPage: "https://enzogsierra.github.io/blogdecafe/",
                stacks: [Stacks.springboot, Stacks.mysql, Stacks.bootstrap, Stacks.javascript, Stacks.jquery]
            },
            {
                name: "Snake Game",
                description: "Recreacin del mtico juego 'Snake' para escritorio. Tiene sistema de colisiones y puntaje",
                imagePath: "snakegame.jpg",
                repositoryName: "snakegame",
                application: "https://enzogsierra.github.io/snakegame/releases",
                stacks: [Stacks.springboot, Stacks.mysql, Stacks.bootstrap, Stacks.javascript, Stacks.jquery]
            },
        ]);
    }, []);

    
    return (
        <main className="container-xl py-5" id="proyectos">
            <h2 className="mb-4 fw-medium text-uppercase text-center">Proyectos acadmicos</h2>

            <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-4">
                {projects.map((project, index) =>
                {
                    return (
                        <div className="col" key={index}>
                            <SideProject project={project} key={index}> </SideProject>
                        </div>
                    );
                })}
            </div>
        </main>
    );
}
